import axios from "axios";
import { CartActionType } from "./action.types";

export const addItem = (item) => ({
  type: CartActionType.ADD_ITEM_TO_CART,
  payload: item,
});

export const clearItemFromCart = (item) => (dispatch, getState) => {
  dispatch({
    type: CartActionType.CLEAR_ITEM_FROM_CART,
    payload: item,
  });

  localStorage.setItem("cartItems", JSON.stringify(getState().cart.cartItems));
};

// export const clearItemFromCart = (item) => ({
//   type: CartActionType.CLEAR_ITEM_FROM_CART,
//   payload: item,
// });

export const addToCartAsync =
  (id, quantity, size, color) => async (dispatch, getState) => {
    const { data } = await axios.get(
      `http://192.168.0.107:5000/api/products/${id}`
    );
    dispatch(
      addItem({ ...data, quantity: quantity, size: size, color: color })
    );
    localStorage.setItem(
      "cartItems",
      JSON.stringify(getState().cart.cartItems)
    );
  };
